
import React from 'react';
export default class SearchMovie extends React.Component{
    constructor(){
        super();
        this.state = {
            keyword:'',
            movies:[]
        }
    }
    keywordChanged = event =>
        this.setState({keyword:event.target.value})
    
    searchMovie = () =>
        //console.log(this.state.keyword)
        fetch(`http://www.omdbapi.com/?apikey=d819527d&s=${this.state.keyword}`)
            .then(response => response.json())
            .then(this.renderMovies)
    renderMovies = (response) =>
        this.setState({
            movies:response.Search
        })
       // console.log(Search)        
    render(){
        return(
            <div className="container">
                <h2>Search Movies</h2>
                <div className="input-group">
                    <input value={this.state.keyword} 
                            onChange={this.keywordChanged}
                            className="form-control" 
                            type="text"
                            placeholder="Enter movie name"
                     />
                    <div className="input-group-append">
                        <button onClick={this.searchMovie} className="btn- btn-primary">Search</button>
                    </div>
                </div>
                <div className="row">
                    {
                        this.state.movies.map(movie => 
                        <div className="col-md-4 movieBox">
                            <div className="movePoster"><img src={movie.Poster} className="img-fluid" /></div>    
                            <h3 className="moveTitle">{movie.Title}</h3>
                            <div className="moveYear">{movie.Year}</div>
                            <div className="moveType">{movie.Type}</div>
                        </div>
                        )
                    }
                </div>
            </div>
        )
    }
}